<template>
    <div class="fight-conv">
        <Banner/>
        <Links
            :links-data="moduleLink"
        />
        <current
            :data="yiQingData"
            :location="location"
        />
        <conv-map
            :data="otherLink"
        />
    </div>
</template>

<script>
import { getFightConvInfo } from '@/api/fightConv';
import config from '@/config';
import { pageFinished } from '@/utils/jsApiV2/client';
import Links from './Links';
import Banner from './Banner';
import Current from './Current';
import ConvMap from './ConvMap';

export default {
    name: 'FightConv',
    data() {
        return {
            moduleLink: [],
            yiQingData: {},
            otherLink: {},
            location: {},
            startView: Date.now(),
        };
    },
    components: {
        Banner,
        Links,
        Current,
        ConvMap,
    },
    methods: {
        init() {
            const params = {
                lon: config.deviceInfo.lo || '',
                lat: config.deviceInfo.la || '',
            };

            getFightConvInfo(params).then((res) => {
                this.$root.hidePlaceholder();

                this.moduleLink = res.moduleLink;
                this.otherLink = res.otherLink;
                this.yiQingData = res.yiQingData;
                this.location = res.location || {};

                // 页面加载完，通知客户端，用于客户端截图
                pageFinished();
            });

            this.$o2o({
                e_c: '肺炎频道',
                e_a: '曝光',
                e_n: '肺炎页面曝光',
            });
        },
        visibleChangeHandler() {
            if (document.hidden) {
                this.$o2o({
                    e_c: '肺炎频道',
                    e_a: '浏览',
                    e_n: '肺炎页面浏览时长',
                    e_x: {
                        duration: Date.now() - this.startView,
                    },
                });
            } else {
                this.startView = Date.now();
            }
        },
    },
    created() {
        this.init();
        document.addEventListener('visibilitychange', this.visibleChangeHandler);
    },
    destroyed() {
        document.removeEventListener('visibilitychange', this.visibleChangeHandler);
    },
};
</script>
