<template>
    <div class="links">
        <div class="links__wrap">
            <ul class="links__list">
                <li
                    v-for="item in linksData"
                    class="links__item"
                    :key="item.title"
                >
                    <a
                        href="javascript:;"
                        @click="goToLinkUrl(item)"
                    >
                        <div class="links__icon">
                            <img
                                :src="item.icon"
                                :alt="item.title"
                            >
                        </div>
                        <p class="links__title">{{ item.title }}</p>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { openCommonWebView } from '@/utils/jsApiV2/client';

export default {
    name: 'Links',
    props: {
        linksData: {
            type: Array,
            default: () => [],
        },
    },
    methods: {
        goToLinkUrl(item) {
            const title = item.title;

            this.$o2o({
                e_c: '肺炎频道',
                e_a: '点击',
                e_n: `${title}按钮点击`,
            });

            openCommonWebView(item.url);
        },
    },
};
</script>

