<template>
    <div class="current">
        <div class="line Hairline Hairline_top"/>
        <div class="title">
            实时疫情<span>数据统计截止 {{ data.time }}</span>
        </div>
        <div class="table">
            <div class="table-tr table-head">
                <div class="table-td">地区</div>
                <div class="table-td">确诊</div>
                <div class="table-td">治愈</div>
                <div class="table-td">死亡</div>
            </div>
            <div
                v-if="total"
                class="table-tr country">
                <div class="table-td">全国</div>
                <div class="table-td">{{ total.sure_cnt }}</div>
                <div class="table-td">{{ total.cure_cnt }}</div>
                <div class="table-td">{{ total.die_cnt }}</div>
            </div>
            <div
                v-if="yst"
                class="table-tr added Hairline Hairline_bottom">
                <div class="table-td">{{ getDate(data.incrTime) }}号新增</div>
                <div class="table-td"><span>+{{ yst.sure_cnt }}</span></div>
                <div class="table-td"><span>+{{ yst.cure_cnt }}</span></div>
                <div class="table-td"><span>+{{ yst.die_cnt }}</span></div>
            </div>
            <div
                v-if="province"
                class="table-tr province Hairline Hairline_bottom">
                <div class="table-td">{{ province.province }}</div>
                <div class="table-td">{{ province.info.sure_cnt }}</div>
                <div class="table-td">{{ province.info.cure_cnt }}</div>
                <div class="table-td">{{ province.info.die_cnt }}</div>
            </div>
            <div
                v-if="city"
                class="table-tr city Hairline Hairline_bottom">
                <div class="table-td">{{ city.city }}</div>
                <div class="table-td">{{ city.sure_cnt }}</div>
                <div class="table-td">{{ city.cure_cnt }}</div>
                <div class="table-td">{{ city.die_cnt }}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Current',
    props: {
        data: {
            type: Object,
            default: () => {},
        },
        location: {
            type: Object,
            default: () => {},
        },
    },
    computed: {
        total() {
            return this.data.total;
        },
        yst() {
            return this.data.yst;
        },
        province() {
            const list = this.data.list;
            const province = this.location.province;

            return this.getInfoByKey(list, 'province', province);
        },
        city() {
            let ret;

            if (this.province) {
                const list = this.province.list;
                const city = this.location.city;

                ret = this.getInfoByKey(list, 'city', city);
            }

            return ret;
        },
    },
    methods: {
        getInfoByKey(list = {}, key, value) {
            const len = list.length;

            for (let i = 0; i < len; i += 1) {
                const val = list[i][key];

                if (val === value || val.indexOf(value) > -1) {
                    return list[i];
                }
            }

            return undefined;
        },
        getDate(timestamp) {
            const time = new Date(timestamp * 1000);

            return time.getDate();
        },
    },
};
</script>

